import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",name: "Home",component: () => import("../views/home/index"), meta: {title: "首页"}
  },
//   {
//     path: "/index",name: "index",component: () => import("../views/home/index"), meta: {title: "首页"}
//   },
  //   登录页面
  {
    path: "/Login",
    name: "Login",
    meta: {
      title: "登录页面"
    },
    component: () => import("../views/login/Login")
  },
  //
  {
    path: "/Forget",
    name: "Forget",
    meta: {
      title: "忘记密码页面"
    },
    component: () => import("../views/login/Forget")
  },
  //   验证码登录页面
  {
    path: "/Smslogin",
    name: "Smslogin",
    meta: {
      title: "验证码登录页面"
    },
    component: () => import("../views/login/Smslogin")
  },
  {
    path: "/email",
    name: "email",
    meta: {
      title: "弹出反馈"
    },
    component: () => import("../views/login/email")
  }
];

const router = new VueRouter({
  routes
});

console.log(router);
// 全局前置守卫
router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  next();
});

export default router;
