// 先进行封装
import Vue from "vue";
import { Toast } from "vant";
import { Guid } from "./guid";

import axios from "axios"; //导入axios

var deviceid = null; //初始化 设备id
console.log(Guid.NewGuid().ToString("D"));
 //62AFDECF-2362-C4F0-A9AD-BE7B1A771ED0
 deviceid = Guid.NewGuid().ToString("D"); //没有就生成
 console.log(deviceid)

// 先获取设备id 如果有 使用之前得，如果没有，生成一个新的
let id = window.localStorage.getItem("deviceid");
console.log(id)
if (id) {
  deviceid = id; //对其赋值
  console.log(deviceid)
} else {
  deviceid = Guid.NewGuid().ToString("D"); //没有就生成
  console.log(deviceid)
}
// 进行存储
window.localStorage.setItem("deviceid", deviceid);

const server = axios.create({
  baseURL: "http://120.53.31.103:84",
  timeout: 5000 //超时时间
});

// 请求拦截
server.interceptors.request.use(config => {
  Toast.loading({
    duration: 5000, //持续展示toast
    message: "加载中……",
    forbidClick: true
  });
  //   登录之后，再次请求时，会携带token进行身份认证
  let token = window.sessionStorage.getItem("token");
  //   console.log(token)
  if (token) {
    config.headers.authorization = `Bearer ${token}`;
  }
  //   设备id
  config.headers.deviceid = `${deviceid}`,
    // 设备类型-固定H5
    (config.devicetype = "H5");
  return config;
});

// 响应拦截
server.interceptors.response.use(res => {
  Toast.clear();
  return res;
});

// get请求方式
server.prototype.get = function(url, params) {
  return server.get(url, { params });
};

// post请求
server.prototype.post = function() {
  return server.post(url, params);
};
export default server;
